@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
body {
  margin: 0;
  font-family: 'Exo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right,#7f5a83 0%, #0d324d 74%);
  
}

button {
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form {
    width: 700px;
    background: rgba(8, 8, 8, 0.1);     
}

p {
    font-size: 50px;
    font-weight: bold;
    color: #fff;
}
.bounding-box {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
    border-style: solid;
    border-color: red;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}



.particles {
  /* 
    z-index is layer, particles will be behind. Works on position fixed, absolute.
    position fixed means image doesn't move even if you scroll
  */
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

